$border-radius: 4px;
$font-size: 16px;

$viewport-height: var(--tg-viewport-height);
$theme-bg-color: var(--tg-theme-bg-color, rgb(36, 34, 49));
$text-color: var(--tg-theme-text-color, rgb(245, 243, 255));
$link-color: var(--tg-theme-link-color, darkslateblue);
$button-color: var(--tg-theme-button-color, darkslateblue);
$button-text-color: var(--tg-theme-button-text-color, #fff);
$secondary-bg-color: var(--tg-theme-secondary-bg-color);

::-webkit-scrollbar {
	width: 8px;
	height: 8px;
	background-color: color-mix(in srgb, $theme-bg-color 85%, #000 15%);
}
::-webkit-scrollbar-thumb {
	background-color: color-mix(in srgb, $button-color 70%, #000 30%);
	border-radius: 9em;
	box-shadow: inset 1px 1px 10px #00000069;
}
::-moz-scrollbar-button:decrement,
::-moz-scrollbar-button:increment,
::-webkit-scrollbar-button:decrement,
::-webkit-scrollbar-button:increment {
	width: 0px;
}

* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	outline: none;
	border: none;
	background: none;
}
body {
	overflow: hidden;
	font-family: sans-serif;
	height: 100vh;
	width: 100%;
	background-color: $theme-bg-color;
	color: $text-color;
	font-size: $font-size;
}
html,
body {
	-webkit-user-select: none;
	-webkit-touch-callout: none;
}

// *[contenteditable] {
// 	-webkit-user-select: auto !important;
// }
.date,
.time {
	position: relative;
	overflow: hidden;
	flex: 1;

	::-webkit-calendar-picker-indicator {
		padding: 2px;

		cursor: pointer;
		background-size: contain;
		background-color: $button-color;
		padding: 3px 6px;
		background-image: none;

		background-position: center;
		position: absolute;
		right: 0;
		height: 100%;
	}
}
.date {
	::-webkit-calendar-picker-indicator {
		background-image: url('../imsges/calendar.svg');
	}
}
.time {
	::-webkit-calendar-picker-indicator {
		background-image: url('../imsges/clock.svg');
	}
}

#root {
	padding: 15px;
	overflow-y: auto;
	height: 100%;
	padding: 20px 20px 30px 20px;
	max-width: 560px;
	margin: 0 auto;
}

.form-wrapper {
	margin-top: 20px;
}

#form {
	&,
	.dinamic-elements,
	& > label {
		display: flex;
		flex-direction: column;
		gap: 15px;
		width: 100%;

		.wrapper {
			display: flex;
			gap: 10px;
			align-items: center;
		}

		.field {
			color: color-mix(in srgb, $button-color 70%, #fff 40%);
			border-radius: $border-radius;
			padding: 12px 8px;
			// border: 1px solid #3e3e3e;
			pointer-events: all;
			flex: 1;
			background: $theme-bg-color;
			filter: brightness(85%);
			width: 100%;
			max-height: 32px;

			&.area {
				min-height: 180px;
				max-height: max-content;
				pointer-events: all;
				display: flex;
				cursor: text;
				text-align: left;
				align-items: flex-start;
				justify-content: flex-start;
				flex-direction: column;
				position: relative;
				resize: vertical;
				flex: auto;
			}
		}
	}
	label {
		gap: 10px;
		width: 100%;
		position: relative;
	}

	.fields-date {
		&__checkbox {
			display: flex;
			align-items: center;
			gap: 20px;

			label {
				width: min-content;
				max-height: 20px;
				max-width: 20px;
				border: 1px solid $button-color;
				transition: 0.2s;

				background-repeat: no-repeat;
				background-position: center;
				background-size: 10px;

				&:has(input:checked) {
					position: relative;
					background-image: url('../imsges/check.svg');
					background-size: 12px;
				}
			}

			input {
				border-radius: $border-radius;
				width: 20px;
				height: 20px;
				display: flex;
				border: 2px #a39ebe78 solid;
				margin: 10px 0;
				cursor: pointer;
				opacity: 0;
			}
		}

		&__wrapper {
			display: flex;
			gap: 20px;
			transition: 0.2s;
			height: 40px;
			align-items: center;
			margin-top: 10px;

			& input {
				width: 100%;
			}

			&.hide {
				height: 0;
			}

			label {
				width: 100%;
			}
		}
	}
}

.btn {
	cursor: pointer;
	padding: 12px;
	border-radius: $border-radius;
	transition: 0.2s;
	border: 2px solid $button-color;
	color: $button-color;
	pointer-events: all;

	&:disabled {
		cursor: not-allowed;
		opacity: 0.5;
	}

	&.select {
		background-color: $button-color;
		color: $button-text-color;
		border-color: $button-color;
	}

	&-submit {
		background-color: $button-color;

		margin-top: 20px;
		color: $button-text-color;
	}
}

.hide {
	opacity: 0;
	visibility: hidden;
}

.people-tag {
	display: flex;
	gap: 3px;
	font-size: 13px;
}

.add-field {
	width: 30px;
	margin-left: auto;
	height: 30px;
	display: flex;
	padding: 0;
	align-items: center;
	justify-content: center;
	font-size: 20px;
	max-height: 32px;
}

.app-form__top-buttons {
	display: flex;
	gap: 10px;
	margin-bottom: 20px;

	& .btn {
		flex: 1;
		padding: 6px;
	}
}
